<template>
  <div id="tarbaritem" @click="routerclick">
     <div :class="{'red':active}"><slot></slot></div>
  </div>
</template>

<script>
export default {
  props:{
  path:String
},
  data(){
return{

}
  },
  computed:{
active(){
  return this.$route.path.indexOf(this.path)!==-1
}
  },
methods:{
  routerclick(){
    this.$router.replace(this.path)
    console.log("点击")
    
  }
}
}
</script>

<style scoped>
#tarbaritem{
 float: left;
  flex: 1;
}
.red{
  color: blue;
  font-style: italic;
}
</style>